/* start style for main contaier  */
.main {
  min-height: 100vh;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
/* end style for main contaier  */
/* start style for header component */
.navbar {
  position: relative;
}
.navbar .mobile-nav {
  position: absolute;
  bottom: -110px;
  width: 100%;
  z-index: 5000;
  display: none;
}
.navbar .mobile-nav.open {
  display: block;
}
@media (max-width: 770px) {
  .order {
    display: none;
  }
  .banner .overlay .intro {
    width: 100%;
  }
}
/* end style for header component */
/* start style for service item */
.servicItem {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}

h2 {
  text-align: center;
  padding: 15px 0;
}
/* end style for service item */
/* start style for home content */
.banner {
  position: relative;
}

.banner .overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
}
.banner .overlay .intro {
  width: 55%;
}
.banner .overlay .intro div {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.banner .overlay .intro h2 {
  color: #fff;
}

.section .title {
  padding: 20px 5px;
  position: relative;
}

.section p {
  font-size: 22px;
}
@media (max-width: 770px) {
  .banner .overlay .intro {
    width: 100%;
  }
  .banner .overlay .intro h2 {
    font-size: 19.5px;
  }
}
/* end style for home content */
/* start footer */
footer .contact p {
  width: 100%;
  text-align: center;
  color: #fff;
}
/* end footer */
